    transform 0.3s cubic-bezier(0.73, 0.01, 0, 1) 0.4s,
    -webkit-transform 0.3s cubic-bezier(0.73, 0.01, 0, 1) 0.4s;
}

[data-tooltip][data-tooltip-conf*='shadow']:after {
